	import {GetUserSpecialList} from 'api/user'
	import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
  export default {
	  components:{
	  	UniLoadMore
	  },
    data() {
      return {
        active: 0,
        list: [],
		moreStatus:"",
        page: 1,
        pageCount: 0,
      };
    },
	onPullDownRefresh() {
		this.getList() 
	},
    onLoad() {
      this.getList();
    },
	/*滚动加载*/
	onReachBottom(){
		this.page++
		if(this.page<=this.pageCount){
			this.getList(this.page)
		}	
	},
    methods: {
      /*滚动加载*/
      onLoad() {
        this.page += 1;
        this.getList(this.page);
      },

      open(item) {
		  if(this.$store.state.isshowprice){
			  uni.navigateTo({
			  	url:'/pages/web/web?url='+encodeURIComponent('/course/'+item.courseid)+'&title='+item.name
			  })
		  }
        
      },
      /*获取列表*/
      getList(p=1) {
		this.page = p
		if(this.page==1){
			this.list = []
		}
		this.moreStatus = 'loading'
		GetUserSpecialList({
			channelid: this.channelid,
			page:this.page,
			pagesize:10,
		}).then(res=>{
			uni.stopPullDownRefresh();
			this.pageCount = res.pagecount
			if(this.page >= this.pageCount){
				this.moreStatus = 'noMore'
			}else{
				this.moreStatus = 'more'
			}
			if(res.result){
				this.list = this.list.concat(res.data)
			}
		})	
        
      }
    }
  };
